
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import axios from 'axios'

export default {
components: {
XHeader,
Group,
XInput,
PopupPicker,
XSwitch,
XButton,
Datetime,
CellFormPreview,
Cell
},
methods: {
toPay: function() {
if (this.name !== '' && this.idNum !== '' && this.telNum !== '') {
var telNum = { telNum: this.telNum };
axios.post('http://localhost:7001/payNumFind', telNum).then((response) => {
if (response.data.data === '') {
localStorage.name = this.name;

location.href = '/#/pay';
}
if (response.data.data === '') {
alert(response.data.data);
}
});
} else {
alert('');
}
}
},
data() {
return {
name: '',
idNum: '',
telNum: '',
people: ['12'],
card: [''],
list1: [['12', '']],
list2: [['', '', '']],
list: [
{
label: " " + localStorage.setout,
value: ""
},
{
label: " " + localStorage.arrive,
value: ""
}
],
readonly: true,
valueReadonly: localStorage.Data,
ticketTime: localStorage.time + ' ',
ticketPrice: '' + localStorage.price
}
}
}



